<template>
  <el-row style="height: 1000px; width: 1200px; margin: 30px auto; text-align: left">
    <!-- 筛选 -->
    <el-row>
      <!-- 关键词搜索 -->
      <el-row style="margin-bottom: 20px">
        <el-input @change="filterChange" placeholder="请输入要搜索的关键词" v-model="filterItem.keyword">
          <template slot="prepend"><i class="el-icon-search"></i></template>
        </el-input>
      </el-row>

      <!-- 品类过滤 -->
      <el-row style="background-color: white;border: 1px solid rgba(200, 200, 200, 0.6); min-height: 60px;padding: 12px 30px 2px 30px">
        <el-col :span="3" style="padding-top: 6px">
          <span style="color: dodgerblue"><i class="el-icon-menu"></i> 品类筛选: </span>
        </el-col>
        <el-col :span="21">
          <el-radio @change="filterChange" v-model="filterItem.productCateGorySelect" v-for="(item, idx) in filterItem.productCateGoryList" :label="item.id" :key="idx" border size="medium" style="margin-bottom: 10px">
            {{ item.label }}
          </el-radio>
        </el-col>
      </el-row>
    </el-row>
    <!-- 数据 -->
    <el-row
      v-loading="dataReqStatus===0"
      element-loading-background="rgba(0, 0, 0, 0.1)"
      style="height: 700px;margin-top: 20px">
      <el-row v-if="dataReqStatus===1 && dataFilter.length>0">
        <el-card @click.native="gotoDetail(item.id)" shadow="hover" class="product-card" v-for="(item, idx) in dataShow" :key="idx" :style="idx%4>0? 'margin-left: 40px': ''" :body-style="{ padding: '0px' }">
          <img :src="item.cover" width="270" height="270" class="image">
          <div style="text-align: center;padding: 8px 20px;font-size: 12px;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;">{{ item.title }}</div>
        </el-card>
      </el-row>
      <el-row v-else-if="dataReqStatus===1 && dataFilter.length===0" style="text-align: center;margin-top: 300px;font-size: 22px;color: darkgrey">
        抱歉！当前搜索过滤条件下没有找到相关产品。
      </el-row>
      <el-row v-else-if="dataReqStatus===-1">
        {{ dataReqErrMsg }}
      </el-row>
      <el-row v-else></el-row>
    </el-row>

    <!-- 分页 -->
    <el-row style="margin-top: 20px;text-align: center">
      <el-pagination
        style="font-size: 16px"
        background
        @current-change="pageChange"
        :current-page.sync="pageSelect"
        :page-size="8"
        layout="prev, pager, next"
        :total="dataFilter.length">
      </el-pagination>
    </el-row>
  </el-row>
</template>

<script>
export default {
  name: 'list',
  data() {
    return {
      filterItem: {
        keyword: '',
        productCateGoryList: [
          { label: '全部', id: 0 }
        ],
        productCateGorySelect: 0
      },
      data: [],
      dataFilter: [],
      dataShow: [],
      dataReqStatus: 0, // 0=加载中, 1=成功, -1=异常
      dataReqErrMsg: '',

      pageSelect: 1
    }
  },
  methods: {
    // 获取商品列表
    async getProductList() {
      this.dataReqStatus = 0
      try {
        const res = await this.$http.get('/api/getProductList', {})
        this.data = res.data.product_lst
        this.filterItem.productCateGoryList = [{ label: '全部', id: 0 }, ...res.data.category]
        // this.data = [
        //   { id: 1, title: 'BL/BLT系列轻型不锈钢立式多级离心泵', category: 1, cover: 'https://www.shimge.com/out/upload/2017/03/16/148963613617490tex8.png' },
        //   { id: 2, title: 'BW/BWJ/BWJT系列轻型不锈钢卧式多级离心泵', category: 1, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896351212339q4zdm.png' },
        //   { id: 3, title: 'AWZB型全自动冷热水旋涡式自吸电泵', category: 1, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896313058211g8gmg.png' },
        //   { id: 4, title: 'JET型喷射式电泵', category: 2, cover: 'https://www.shimge.com/out/upload/2017/03/16/1489630588908r8ff4.png' },
        //   { id: 5, title: 'JET型不锈钢喷射式电泵', category: 1, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896301200207y34b9.png' },
        //   { id: 6, title: 'SGL(R)型单级单吸立式离心泵', category: 2, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896292768011tnyze.png' },
        //   { id: 7, title: 'SGR型管道式离心泵', category: 1, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896287420335qj0ph.png' },
        //   { id: 8, title: 'WZB型旋涡式自吸电泵', category: 1, cover: 'https://www.shimge.com/out/upload/2018/01/26/151692758015ysue4.jpg' },
        //   { id: 9, title: 'BL/BLT系列轻型不锈钢立式多级离心泵', category: 2, cover: 'https://www.shimge.com/out/upload/2017/03/16/148963613617490tex8.png' },
        //   { id: 10, title: 'BW/BWJ/BWJT系列轻型不锈钢卧式多级离心泵', category: 2, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896351212339q4zdm.png' },
        //   { id: 11, title: 'AWZB型全自动冷热水旋涡式自吸电泵', category: 2, cover: 'https://www.shimge.com/out/upload/2017/03/16/14896313058211g8gmg.png' }
        // ]
        this.filterChange()
        this.dataReqStatus = 1
      } catch (err) {
        this.dataReqErrMsg = err
        this.dataReqStatus = -1
      }
    },
    filterChange() {
      let dataFilter = this.data

      // keyword
      const keyword = this.filterItem.keyword.trim().toLowerCase()
      if (keyword) {
        dataFilter = dataFilter.filter(item => {
          return item.title.toLowerCase().includes(keyword)
        })
      }

      // category
      if (this.filterItem.productCateGorySelect) {
        dataFilter = dataFilter.filter(item => {
          return item.category === this.filterItem.productCateGorySelect
        })
      }

      this.dataFilter = dataFilter
      this.pageSelect = 1
      this.pageChange()
    },
    pageChange() {
      this.dataShow = this.dataFilter.slice((this.pageSelect - 1) * 8, this.pageSelect * 8)
    },
    gotoDetail(id) {
      this.$router.replace(`/list/detail?id=${id}`)
    },
    sleepMs(ms) {
      return new Promise((resolve) => {
        setTimeout(resolve, ms);
      });
    }
  },
  created() {
  },
  mounted() {
    this.filterItem.keyword = ''
    this.filterItem.productCateGorySelect = 0
    this.getProductList()
  }
}
</script>

<style scoped>
  .product-card {
    margin-bottom: 40px;
    width: 270px;
    display: inline-block;
    cursor: pointer;
  }
  .product-card:hover {
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
    transform: translate3d(0, -3px, 0);
    transition: all 20ms linear;
  }
  .product-card:hover div {
    background-color: var(--second-color);
    color: white;
  }
</style>
